.colorBarBox {
  width: 100%;
  padding: 12px 12px 0 11px;
  box-sizing: border-box;
  /*max-height: 418px;*/
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.colorBarBox::-webkit-scrollbar {
  display: none;
}

.colorBarBox::-moz-scrollbar {
  display: none;
}

.colorBarBox::-ms-scrollbar {
  display: none;
}

.colorBarBox::-o-scrollbar {
  display: none;
}

.colorBar {
  width: 100%;
  height: 48px;
  margin-bottom: 10px;
  box-sizing: border-box;
}

.paintColorList {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 85px;
  height: 25px;
  z-index: 10;
  text-align: center;
}

.brushBar {
  /*height: 55px;*/
  text-align: center;
  border-bottom: 1px solid #dadada;
  /*line-height: 55px;*/
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px;
  line-height: 24px;
}
.brushBar:last-child{
  border: none
}


