.paintColorList > div {
  position: relative;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  display: inline-block;
  vertical-align: middle;
}

.paintColorList > span {
  display: inline-block;
  background-color: #000;
  width: 6px;
  height: 2px;
  margin: 0 7px;
  vertical-align: middle;
  opacity: 1;
}

.selectColorMode.paintColorList > span {
  opacity: 0;
}

.paintColorList > div.active {
  height: 20px;
  width: 20px;
}

.paintColorList > div.active:before {
  display: inline-block;
  content: "";
  height: 24px;
  width: 24px;
  border: 2px solid #6a6a6a;
  border-radius: 50%;
  position: absolute;
  left: -4px;
  top: -4px;
}
